import React, { Component } from "react";
import Hotel from "./../../components/hotel";
import { Input, TextArea, FormBtn, } from "../../components/Form";
import { Container } from "../../components/Grid";
import API from './../../utils/API'


function reloadPage()
{
  window.location.reload();
}

class Hotels extends Component {
state = {
    hotellist: [],
    text: "",
    location: "",
    author: ""
  };

  componentDidMount() {
    this.loadHotels();
  };

  loadHotels = () => {
    API.getHotels()
      .then(res =>
        this.setState({ hotel: res.data, location: "", author: "", text: "" })
      )
      .catch(err => console.log(err));
  };

  deleteHotel = id => {
    API.deleteHotel(id)
      .then(res => this.loadHotels())
      .catch(err => console.log(err));
  };

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleFormSubmit = event => {
    event.preventDefault();
    if (this.state.location && this.state.author) {
      API.saveHotel({
        location: this.state.location,
        author: this.state.author,
        text: this.state.text
      })
        .then(res => this.loadHotels())
        .catch(err => console.log(err));

    }

    reloadPage();

  };


render (){
  return (
    <main>
      <div className="row">
        <div className="col s12">
          <div className="card-panel hotels">
            <div className="row">
              <div className="col s4">
                <h3>Hotels</h3>
              </div>
            </div>
            <div className="row">
              <div className="col s6">
                <form onSubmit={this.handleSubmit}>
                  <Input
                    value={this.state.location}
                    onChange={this.handleInputChange}
                    name="location"
                    placeholder="Location (required)"
                  />
                  <Input
                    value={this.state.author}
                    onChange={this.handleInputChange}
                    name="author"
                    placeholder="Author (required)"
                  />
                  <TextArea
                    value={this.state.text}
                    onChange={this.handleInputChange}
                    name="text"
                    placeholder="Text (Optional)"
                  />
                  <FormBtn
                    disabled={!(this.state.location && this.state.author)}
                    onClick={this.handleFormSubmit}
                  >
                    Submit Hotel
                  </FormBtn>
                </form>
              </div>
              <div className="col s6">
              <p>
                    <Hotel />
                  </p>
              </div>
            </div>
            <div className="row">
              <div className="col s12">
                <div className="container">
                  {/* <p>
                    <Review />
                  </p> */}
                  {/* <div className="card-panel hotel"></div>
                                            <div className="card-panel hotel"></div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};
};

export default Hotels;



